.createUserContainer {
  flex: 6;
  padding: 20px;
}
.newUserTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  background-color: dodgerblue;
  color: aliceblue;
  padding: 5px;
}
.createUserForm {
  display: flex;
  flex-wrap: wrap;
}
.newUserItem {
  width: 400px;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-right: 20px;
}
.newUserItem > label {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 600;
  color: gray;
}
.newUserItem > input {
  height: 20px;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 5px;
}
.newUserGender > input {
  margin-top: 15px;
}
.newUserGender > label {
  margin: 10px;
  font-size: 18px;
  color: #555;
}
.newUserSelect {
  height: 40px;
  border-radius: 5px;
}
.newUserButton {
  width: 200px;
  border: none;
  border-radius: 10px;
  background-color: dodgerblue;
  color: aliceblue;
  padding: 7px 10px;
  font-weight: 600;
  margin-top: 30px;
  cursor: pointer;
}
