.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.featuredItem {
  flex: 1;
  margin: 0px 20px;
  margin-top: 10px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  background-color: antiquewhite;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}
