.productListContainer {
  flex: 6;
}
.productListItem {
  display: flex;
  align-items: center;
  margin: 10px;
}
.productListImg {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  object-fit: cover;
}
.productListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: dodgerblue;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}
.productDelete {
  color: red;
  cursor: pointer;
}
