.topnavContainer {
  width: 100%;
  height: 50px;
  background-color: aliceblue;
  position: sticky;
  top: 0;
  z-index: 999;
}
.topnavWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-weight: bold;
  font-size: 30px;
  color: aliceblue;
  cursor: pointer;
  background-color: red;
  border-radius: 5px;
  font-family: monospace;
  margin-top: 3px;
  padding: 5px;
}
.topRight {
  display: flex;
  align-items: center;
}
.topnavIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
}
.topIconBadge {
  width: 15px;
  height: 15px;
  position: absolute;
  top: -5px;
  right: 5px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}
.topnavIcon {
  color: dodgerblue;
}
.adminImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
